.inner-hexagon {
  position: absolute;
}

.inner-hexagon {
  height: 201.25px;
  left: 53.9%;
  top: 40%;
  width: 350px;
  z-index: 3
}

.inner-hexagon .ihcenter {
  border-color: transparent #fff;
  border-style: solid;
  border-width: 1px;
  height: 25%;
  left: 0;
  position: absolute;
  top: 0;
  width: 25%
}

.inner-hexagon .ihcenter.ihcenter2 {
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg)
}

.inner-hexagon .ihcenter.ihcenter3 {
  -webkit-transform: rotate(-60deg);
  transform: rotate(-60deg)
}

.outer-hexagon {
  height: 201.25px;
  left: 50%;
  position: absolute;
  top: 35%;
  width: 350px;
  z-index: 3
}

.outer-hexagon .ohcenter {
  border-color: transparent hsla(0, 0%, 77%, .453);
  border-style: solid;
  border-width: 1px;
  height: 40%;
  left: 0;
  position: absolute;
  top: 0;
  width: 40%
}

.outer-hexagon .ohcenter.ohcenter2 {
  -webkit-transform: rotate(60deg);
  transform: rotate(60deg)
}

.outer-hexagon .ohcenter.ohcenter3 {
  -webkit-transform: rotate(-60deg);
  transform: rotate(-60deg)
}

