  }
  
  .screendown {
    position: fixed;
    width: 100%;
    height: 0;
    overflow: hidden;
  }
  
  .screendown-content {
    position: fixed;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  

  .moveUpAnimation {
    animation: moveUp 1.5s ease-out forwards;
    justify-content: center;
    align-items: center;
  }
  
  @keyframes moveUp {
    from {
      transform: translateY(0);
      opacity: 1;
    }
    to {
      transform: translateY(-100%);
      opacity: 0;
    }
  }
  
  
  @keyframes screendownAnimation {
    from {
      height: 0;
    }
    to {
      height: 100%;
    }
  }
  
  
  @keyframes slideDown {
    from {
      transform: translateY(-100%);
    }
    to {
      transform: translateY(0);
    }
  }

